<script>
import { Line } from 'vue-chartjs';

export default {
  extends: Line,
  props: ['data', 'isDaily', 'labels'],
  mounted() {
    let xLabel = '';

    const chartdata = {
      labels: [],
      datasets: [
        {
          label: this.labels[0],
          data: [],
          backgroundColor: '#3e95cd',
        },
      ],
    };

    this.data.forEach(element => {
      const time = new Date(element.created_at);

      if (this.isDaily) {
        chartdata.labels.push(time.toLocaleDateString());
      } else {
        chartdata.labels.push(time.toLocaleTimeString('fr-FR', { hour: '2-digit', minute: '2-digit' }));
      }

      if (this.labels[0] === 'Ping') {
        chartdata.datasets[0].data.push(element.ping);
      } else {
        chartdata.datasets[0].data.push(element.uptime);
      }
    });

    if (this.isDaily) {
      xLabel = 'Day';
    } else {
      xLabel = 'Time';
    }

    const options = {
      legend: {
        labels: {
          fontColor: 'white',
          fontSize: 15,
        },
      },
      scales: {
        xAxes: [{
          ticks: {
            fontColor: 'white',
            fontSize: 15,
            stepSize: 1,
            beginAtZero: true,
          },
          scaleLabel: {
            display: true,
            labelString: xLabel,
            fontColor: 'white',
            fontSize: 20,
          },
        }],
        yAxes: [{
          ticks: {
            fontColor: 'white',
            fontSize: 15,
            stepSize: 10,
            beginAtZero: true,
          },
          scaleLabel: {
            display: true,
            labelString: this.labels[1],
            fontColor: 'white',
            fontSize: 20,
          },
        }],
      },
      maintainAspectRatio: false,
    };

    this.renderChart(chartdata, options);
  },
};
</script>

