<template>
    <div class="service" style="padding-top: 1em; padding-bottom: 1em">
        <Minecraft v-if="service.db.service == 'Minecraft'" v-bind:service="service"
        v-bind:updated="updated"></Minecraft>
        <SRCDS v-if="service.db.service == 'SRCDS'" v-bind:service="service"
        v-bind:updated="updated"></SRCDS>
        <Web v-if="service.db.service == 'Web'" v-bind:service="service"
        v-bind:updated="updated"></Web>

        <div v-if="service.db.service !== 'Web'" class="card mb-3">
          <h3 class="card-header">Metrics ({{ titleString }})
            <button v-on:click="showGraphs = !showGraphs"
            class="btn btn-primary btn-sm float-right">Toggle Graphs</button>
          </h3>
          <button v-if="showGraphs" v-on:click="switchIsDaily"
            class="btn btn-primary btn-sm float-right">Toggle {{ buttonString }}</button>
          <div style="max-height: 100em;" v-if="showGraphs">
            <div v-if="isDaily">
              <ServiceChart v-if="metricsLoaded" :data="metrics_daily" :isDaily="isDaily"
              :labels="['Ping', 'Ping (ms)']"></ServiceChart>
              <hr>
              <ServiceChart v-if="metricsLoaded" :data="metrics_daily" :isDaily="isDaily"
              :labels="['Uptime', 'Uptime (%)']"></ServiceChart>
            </div>
          </div>
          <div style="max-height: 100em;" v-if="showGraphs">
            <div v-if="!isDaily">
              <ServiceChart v-if="metricsLoaded" :data="metrics_hourly" :isDaily="isDaily"
              :labels="['Ping', 'Ping (ms)']"></ServiceChart>
              <hr>
              <ServiceChart v-if="metricsLoaded" :data="metrics_hourly" :isDaily="isDaily"
              :labels="['Uptime', 'Uptime (%)']"></ServiceChart>
            </div>
          </div>
        </div>
        <ol class="breadcrumb">
          <li class="breadcrumb-item"><a href="/">Services</a></li>
          <li class="breadcrumb-item">
            <a :href="`/services/${this.$route.params.id}`">
              {{ service.db.name }}
            </a>
          </li>
        </ol>
    </div>
</template>
<script>
import axios from 'axios';
import Minecraft from '../components/Minecraft.vue';
import SRCDS from '../components/SRCDS.vue';
import Web from '../components/Web.vue';
import ServiceChart from '../components/ServiceChart.vue';

export default {
  data() {
    return {
      service: [],
      updated: null,
      metrics_hourly: [],
      metrics_daily: [],
      metricsLoaded: false,
      isDaily: false,
      buttonString: 'Daily',
      titleString: 'Hourly',
      showGraphs: true,
    };
  },
  methods: {
    retrieveServiceData() {
      axios
        .get(`${this.$url}/services/${this.$route.params.id}`)
        .then(response => (this.service = response.data));
      this.updated = new Date();
    },
    retrieveMetricData() {
      axios
        .get(`${this.$url}/metrics/${this.$route.params.id}?timeframe=hourly`)
        .then(response => (this.metrics_hourly = response.data));
      axios
        .get(`${this.$url}/metrics/${this.$route.params.id}?timeframe=daily`)
        .then(response => (this.metrics_daily = response.data))
        .then(this.metricsLoaded = true);
    },
    switchIsDaily() {
      if (this.isDaily) {
        this.buttonString = 'Daily';
        this.titleString = 'Hourly';
      } else {
        this.buttonString = 'Hourly';
        this.titleString = 'Daily';
      }
      this.isDaily = !this.isDaily;
    },
  },
  mounted() {
    this.retrieveServiceData();
    this.retrieveMetricData();
    setInterval(() => {
      this.retrieveServiceData();
    }, 30000);
  },
  components: {
    Minecraft,
    SRCDS,
    Web,
    ServiceChart,
  },
};
</script>
