<template>
    <div class="card mb-3" id="SRCDS">
        <h3 class="card-header">{{ service.db.name }}</h3>
        <ul class="list-group list-group-flush">
        <li class="list-group-item">Status:
            <span class="text-success" v-if="service.online"> Online</span>
            <span class="text-danger" v-else> Offline</span>
        </li>
        <li class="list-group-item">Ping: {{ Number((service.ping).toFixed(1)) }} ms</li>
        <li class="list-group-item">Game: {{ service.game }}</li>
        <li class="list-group-item">Map: {{ service.map_name }}</li>
        <li class="list-group-item">
            Players: {{ service.player_count }}/{{ service.max_players }}
            <button v-if="service.player_count > 0" v-on:click="showPlayers = !showPlayers"
            class="btn btn-primary btn-sm float-right">Toggle Players</button>
        </li>
        <div v-if="service.player_count > 0 && showPlayers" class="card-footer">
            <p v-for="player in service.players" v-bind:key="player.name">
                {{ player.name }} (Online for {{ Number((player.duration / 60).toFixed(0)) }} mins)
            </p>
        </div>
        <li class="list-group-item">Version: {{ service.version }}</li>
        </ul>
        <div class="card-footer text-muted">
            Last Updated: {{ this.updated.toLocaleTimeString() }}
        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      showPlayers: false,
    };
  },
  props: {
    service: Object,
    updated: Object,
  },
};
</script>
