import Vue from 'vue';
import VueRouter from 'vue-router';
import Index from '../views/Index.vue';
import Service from '../views/Service.vue';
import Gmod from '../views/Gmod.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Index',
    component: Index,
  },
  {
    path: '/services/:id',
    name: 'Service',
    component: Service,
  },
  {
    path: '/gmod',
    name: 'Gmod',
    component: Gmod,
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  linkExactActiveClass: 'active',
});

export default router;
