<template>
    <div class="index">
        <p style="padding-top: 1em; padding-bottom: 1em">
            Below is a table listing all services that are being hosted.
            Please see the Garry's Mod tab for the workshop
            collection and information on how to install CS:Source and connect to the server.
            The default IP is: <strong>m311.ddns.net</strong>
        </p>
        <h5>Services</h5>
        <table class="table table-light" id="table">
            <thead>
                <tr>
                    <th scope="col">Name</th>
                    <th scope="col">Service</th>
                    <th scope="col">Port</th>
                    <th scope="col">Mods</th>
                    <th scope="col">Status</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="service in services" v-bind:key="service.db.id" class="table-dark">
                    <td v-html="service.db.name"></td>
                    <td>{{ service.db.service }}</td>
                    <td>{{ service.db.port }}</td>
                    <td v-html="service.db.mods"></td>
                    <td v-if="service.online">
                      <router-link class="text-success"
                      :to="{ name: 'Service', params: { id: service.db.id }}">
                        Online
                      </router-link>
                    </td>
                    <td v-else>
                      <router-link class="text-danger"
                      :to="{ name: 'Service', params: { id: service.db.id }}">
                        Offline
                      </router-link>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
import axios from 'axios';

export default {
  data() {
    return {
      services: [],
    };
  },
  methods: {
    retrieveData() {
      axios
        .get(`${this.$url}/services`)
        .then(response => (this.services = response.data));
    },
  },
  mounted() {
    this.retrieveData();
    setInterval(() => {
      this.retrieveData();
    }, 10000);
  },
};
</script>
